/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --font-body: "Lato", sans-serif;
  --font-display: "League Spartan", sans-serif;
}

:root {
  --nwcGreen: hsl(167, 100%, 20.2%);
  --nwcLightGreen: hsl(120, 55.5%, 44.9%);
  --nwcRed: hsl(355, 84.5%, 54.5%);
  --nwcYellow: hsl(47, 100%, 46.1%);
  --nwcBlue: hsl(220, 100%, 23.1%);
  --nwcLightBlue: hsl(207, 100%, 40.6%);
  --green: hsl(166, 100%, 18.4%);
  --lightgreen: hsl(130, 40.5%, 52.5%);
  --fadeRed: hsl(360, 82.5%, 57.5%);
  --brightRed: hsl(358, 72.6%, 45.9%);
  --red: hsl(358, 84.6%, 51.8%);
  --darkBlue: hsl(235, 52.2%, 9%);
  --gold: hsl(54, 69.2%, 84.7%);
  --lightGold: hsl(54, 71.4%, 97.3%);
  --black: hsl(0, 0%, 0%);
  --richBlack: hsl(0, 0%, 6%);
  --lightGrey: hsl(210, 1%, 60.8%);
  --grey: hsl(0, 0%, 50.2%);
  --white: hsl(0, 0%, 100%);
}

.schedules {
  margin-bottom: 2.5rem;
}

table {
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout: fixed;
}

table caption {
  font-size: 1.5em;
  margin: 0.5em 0 0.75em;
  text-align: left;
  color: var(--nwcGreen);
}

table tr {
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  padding: 0.35em;
}

table th,
table td {
  padding: 0.625em;
  text-align: center;
}

table th {
  font-size: 0.85em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

@media screen and (max-width: 600px) {
  table {
    border: 0;
  }
  table caption {
    font-size: 1.3em;
  }
  table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  table tr {
    border-bottom: 3px solid #ddd;
    display: block;
    margin-bottom: 0.625em;
  }
  table td {
    border-bottom: 1px solid #ddd;
    display: block;
    font-size: 0.8em;
    text-align: right;
  }
  table td::before {
    /*
    * aria-label has no advantage, it won't be read inside a table
    content: attr(aria-label);
    */
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }
  table td:last-child {
    border-bottom: 0;
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

a {
  color: var(--nwcGreen);
  font-weight: 700;
  letter-spacing: 0.0625rem;
}
a:hover {
  text-decoration: underline;
  color: var(--nwcLightGreen);
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-weight: 500;
  color: var(--black);
  line-height: 1.3;
}
body.noscroll {
  overflow: hidden;
}

.container {
  width: 80%;
  max-width: 75rem;
  margin: 0 auto;
}
.container__content {
  width: 60%;
  margin-bottom: 2.5rem;
}
@media (max-width: 59.999375em) {
  .container__content {
    width: 80%;
  }
}
@media (max-width: 43.74875em) {
  .container__content {
    width: 100%;
  }
}

.row {
  padding: 0 1.25rem;
}

.breadcrumbs {
  margin-bottom: 2.5rem;
}

.breadcrumb {
  font-family: var(--font-body);
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--lightGrey);
}
.breadcrumb__current {
