}

.btn__contact a, .btn__login a {
  font-family: var(--font-display);
  font-size: 1.125rem;
  font-weight: 500;
  text-transform: uppercase;
  padding: 0.625rem 1.25rem;
  color: var(--nwcGreen);
  background-color: var(--white);
  border: 0.0625rem solid var(--white);
  border-radius: 0.3125rem;
  margin-left: 1.25rem;
  cursor: pointer;
}
@media (max-width: 59.999375em) {
  .btn__contact a, .btn__login a {
    margin-left: 0;
  }
}
.btn__contact a:hover, .btn__login a:hover {
  background-color: var(--nwcLightGreen);
  border: 0.0625rem solid var(--nwcLightGreen);
  color: var(--white);
  transition: color 250ms ease-in-out;
}
.btn__center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn__back {
  font-family: var(--font-display);
  font-size: 1.125rem;
  font-weight: 500;
  text-transform: uppercase;
  padding: 0.625rem 1.25rem;
  color: var(--white);
  background-color: var(--nwcGreen);
  border: 0.0625rem solid var(--nwcGreen);
  border-radius: 0.3125rem;
  margin-left: 1.25rem;
  cursor: pointer;
}
.btn__back:hover {
  background-color: var(--nwcLightGreen);
  border: 0.0625rem solid var(--nwcLightGreen);
  color: var(--white);
  text-decoration: none;
  transition: color 250ms ease-in-out;
}

.btn.btn__login.footer__nav--login {
  color: var(--nwcGreen);
}
@media (max-width: 59.999375em) {
  .btn.btn__login.footer__nav--login {
    margin-top: 1.25rem;
    margin-bottom: 1.875rem;
    text-align: center;
  }
}
.btn.btn__login.footer__nav--login:hover {
  color: var(--white);
}

.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

.header {
  background-color: var(--nwcGreen);
  display: flex;
  flex-direction: row;
  align-items: center;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 998;
}
.header.open .overlay {
  display: block;
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(var(--nwcLightGreen), transparent);
}
.header__logo {
  padding: 1rem 0;
}
.header__logo img {
  width: 10.9375rem;
  height: 5.3125rem;
}
.header__section {
  width: 100%;
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: var(--white);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
.header__nav {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.header__nav--open {
  position: absolute;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: var(--nwcGreen);
  width: 100%;
  left: 50%;
  transform: translate(-50%);
  height: auto;
  top: 125px;
  padding: 20px;
  z-index: 990;
  margin: 0 auto;
  transition: all 300ms ease-in-out;
}
.header__media {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 0.625rem;
}
